// CONFIGURATION VALIDATION  SCHEMA
export const VALIDATION_BACKENDMESSAGE_INVALIDORGID = "No Configuration Found";

export const isAnExistingConfiguration = configuration => {
  if (
    configuration !== null &&
    typeof configuration.environments === "object" &&
    Object.keys(configuration.environments).length > 0
  ) {
    return true;
  }

  return false;
};

export const mapBackendErrorMessage = error => {
  if (error === VALIDATION_BACKENDMESSAGE_INVALIDORGID) {
    return {
      heading: "Cannot find this SFDC Org ID in the configuration of the selected Heroku app.",
      details: `Please make sure the selected Heroku app does not already have a configuration tied to another SFDC Org.
              Each Heroku app can only have a single SFDC source org configured, and this cannot be changed once configured.`
    };
  }

  return error;
};
