import React, { useState } from "react";
import PropTypes from "prop-types";

import { CONNECTION_PROVIDERS_SFDC } from "../../../consts/configurations/connectionProviders";

import VendorValidityMessage from "../VendorValidityMessage";
import StatefulInput from "../StatefulInput";
import SalesforceLogin from "./SalesforceLogin";
import OAuthLogin from "./OAuthLogin";

import useSalesforceConnection from "../../../hooks/configuration/useSalesforceConnection";
import { useAutoDismissToast } from "../../../hooks/configuration/useIndividualVendorValidity";
import ConfigurationNotice from "../ConfigurationNotice";

const propTypes = {
  showConnectionName: PropTypes.bool,
  variant: PropTypes.oneOf(["sourceDataSource", "syncSalesforceEinstein", "bulkRestoreDestination"])
};

const SalesforceConnection = ({ variant, showConnectionName = false }) => {
  const [validation, setValidationStatus] = useState("");
  const isOpen = useAutoDismissToast(validation);

  const [
    { userName, password, token, url, name, organizationId, selectedTab },
    { onBlur }
  ] = useSalesforceConnection(variant);

  return (
    <>
      {variant === "sourceDataSource" && (
        <ConfigurationNotice
          text={`Please enter credentials for the integration user the GRAX app will use to log in. In order
        to prevent crossing your wires, the credentials entered here must be for this current SFDC
        org you are in.`}
        />
      )}

      {isOpen === true && <VendorValidityMessage variant={variant} status={validation.status} />}
      {showConnectionName === true && (
        <div className="slds-grid slds-gutters_small slds-m-bottom_small">
          <div className="slds-col slds-size_2-of-3">
            <StatefulInput
              label="Connection Name"
              required
              value={name}
              id="name"
              helpText='Please name your Salesforce connection e.g. "Dev Sandbox'
              onBlur={onBlur}
            />
          </div>
        </div>
      )}

      {selectedTab === CONNECTION_PROVIDERS_SFDC ? (
        <SalesforceLogin
          variant={variant}
          userName={userName}
          password={password}
          token={token}
          url={url}
          name={name}
          organizationId={organizationId}
          onBlur={onBlur}
          onValidated={setValidationStatus}
        />
      ) : (
        <OAuthLogin />
      )}
    </>
  );
};

SalesforceConnection.propTypes = propTypes;

export default SalesforceConnection;
